.button {
    position: relative;
    margin: 10px;
    overflow: hidden;
    cursor: pointer;
}

.button:disabled {
    background: #5a8cbd;
}

.default {
    width: 132px;
    height: 30px;

    background: #0066CC;

    border: 3px solid #4c94de;
    border-radius: 5px;
    color: #FFFFFF;

    padding: 5px 24px 5px 24px;

    gap: 10px;
    outline: none;

}

.red {
    width: 132px;
    height: 30px;

    color: #FFFFFF;
    background: #CC1439;

    border: 3px solid #ce2e4f;
    border-radius: 5px;
}

.red:active {
    background: #B21232;
}

.red:hover {

}

.disabled {
    background: #5a8cbd;
    color: #FFFFFF;
    border: 3px solid #5a8cbd;

    cursor: auto;
    width: 132px;
    height: 30px;
}

.secondary {
    width: 132px;
    height: 30px;

    color: #0066CC;
    background: #FFFFFF;

    border: 1px solid #0066CC;
    border-radius: 5px;

}

.default:hover {
    background: #0080FF;

}

.secondary:hover {
    background: #E5F0FA;

    border: 3px solid blueviolet;
}

.default:active {
    background: #0059B3;
}

.secondary:active {
    background: #C9E5FF;
}




